<template>
    <div id="frame">
        <el-container style="height: 600px; border: 1px solid #eee">
            <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
                <el-menu :default-openeds="['1']">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-s-comment"></i>评论管理
                        </template>
                        <el-menu-item-group>
                            
                            <el-menu-item
                                index="jiangan"
                                :class="$route.path=='/jiangan'?'is-active':''"
                                @click="changePage('Jiangan')"
                            >江安校区</el-menu-item>
                            <el-menu-item
                                index="wangjiang"
                                :class="$route.path=='/wangjiang'?'is-active':''"
                                @click="changePage('Wangjiang')"
                            >望江校区</el-menu-item>
                            <el-menu-item
                                index="wangxi"
                                :class="$route.path=='/huaxi'?'is-active':''"
                                @click="changePage('Huaxi')"
                            >华西校区</el-menu-item>
                            <el-menu-item
                                index="xiaoyou"
                                :class="$route.path=='/xiaoyou'?'is-active':''"
                                @click="changePage('Xiaoyou')"
                            >知名校友</el-menu-item>
                        
                        </el-menu-item-group>
                        
                    </el-submenu>
                </el-menu>
            </el-aside>

            <el-container style="border: 1px solid #eee;width: 500px">
                <el-header style="text-align: right; font-size: 18px">
                    <span>{{manager.name}}</span>
                    <el-dropdown @command="handleCommand">
                        <i class="el-icon-setting" style="margin: 15px; size:auto"></i>
                        <el-dropdown-menu slot="dropdown">
                            <el-dropdown-item command="a">查看</el-dropdown-item>
                            <el-dropdown-item command="b">返回主页面</el-dropdown-item>
                            
                            <el-dropdown-item command="c">退出登录</el-dropdown-item>
                        </el-dropdown-menu>
                    </el-dropdown>
                </el-header>

                <router-view></router-view>
            </el-container>
        </el-container>
    </div>
</template>

<script>
export default {
    name: "frame",
    data() {
        return {
            manager: this.$route.params.manager,
        };
    },
    methods: {
        changePage: function (destination) {
            if (this.$route.name !== destination) {
                this.$router.push({
                    name: destination,
                    params: {
                        manager: this.manager,
                    },
                });
            }
        },
        handleCommand(command) {
            var that = this;
            if (command == 'b'){
                window.location.href = './index.html';
                // window.location.href = 'http://baidu.com';
            }
            if (command == "c"){
                that.changePage("Login");
            }
        },
        
    },
};
</script>

<style>
#Frame {
    font-family: "Avenir", Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
.el-header {
    background-color: #b3c0d1;
    color: #333;
    line-height: 60px;
}

.el-aside {
    color: #333;
}
</style>
