import Vue from "vue";
import VueRouter from "vue-router";

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "Login",
    component: () => import("@/views/Login"),
    meta: {
      title:"川越精彩评论管理"
    }
  },
  {
    path: "/Frame",
    name: "Frame",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/Frame"),
    meta: {
      title:"川越精彩评论管理"
    },
    children: [
      {
        path: '/jiangan',
        name: 'Jiangan',
        component: () =>import("../views/Jiangan")
      },
      {
        path: '/wangjiang',
        name: 'Wangjiang',
        component:()=>import("../views/Wangjiang")
      },
      {
        path: '/huaxi',
        name: 'Huaxi',
        component:()=>import("../views/Huaxi")
      },
      {
        path: '/xiaoyou',
        name: 'Xiaoyou',
        component:()=>import("../views/Xiaoyou")
      }
    ]
  }
  
];

const router = new VueRouter({
  // mode: "history",
  base: process.env.BASE_URL,
  routes,
});

router.beforeEach((to, from, next) => {
  if (to.meta.title) {
    document.title = to.meta.title
  }
  next();
});

export default router;
